$MainColor: #ff0000;

.red {
  color: $MainColor;
}

@mixin 함수() {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
  p {
    text-align: center;
    margin-bottom: 0;
  }
}
.my-alert {
  @include 함수();
}
.my-alert2 {
  @extend .my-alert;
  background-color: #ffe591;
}
